<script>
import { supabase, websitename } from "../supabase";
import FooterComponent from "@/components/Footer.vue";
import NavBar from "@/components//Navbar2.vue";

export default {
  name: "login-page",
  components: {
    FooterComponent,
    NavBar,
  },
data() {
  return {
    email: "",
    password: "",
    success: false,  
    errors: null,
    loading: false,  
    website: websitename,
  };
},
  methods: {
  async handleSignin() {
    try {
      this.loading = true
      // Use the Supabase provided method to handle the signup
      const { error } = await supabase.auth.signInWithPassword({
        email: email.value,
        password: password.value,
      });
      this.success = true;
      setTimeout(() => {
        this.$router.push('/')
      }, 1000)

      if (error) throw error;
      } catch (error) {
        this.success = false;
        this.errors = error.message;
      } finally {
        this.loading = false
      }
  },
}
}
</script>